.popular-now-title {
  font-size: var(--header-300);
  font-weight: bold;
  margin-top: 32px;
  margin-bottom: 32px;
  color: var(--white);
  text-align: left;
  width: 100%;
}

.films-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.film-card {
  max-width: 284px;
  width: 100%;
  height: 400px;
  margin: 16px 0 16px 0;
  background-size: cover;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 0 10px var(--shadow-color);
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  background: var(--neutral-light) center;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:hover {
    transform: scale(1.05);
  }
}

.film-content {
  display: flex;
  gap: 64px;
  align-items: center;
  justify-content: space-between;
  padding-top: 16px;
}

.film-image {
  width: 87%;
  height: 80%;
  background-size: cover;
  background-position: center;
  border-radius: 15px;
  margin-top: 20px;
  object-fit: cover;
}

.film-time {
  display: flex;
  font-size: var(--caption);
  background-color: var(--neutral-main);
  color: var(--grey-secondary);
  padding: 4px 8px;
  border-radius: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.film-title {
  color: var(--white);
  margin-right: 16px;
  display: flex;
  align-items: center;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.films-section {
  border: 2px solid var(--border-color);
  padding: 16px 32px;
  border-radius: 16px;
  margin-top: 32px;
  width: 1200px;
}

.series-section {
  border: 2px solid var(--border-color);
  padding: 16px 32px 96px;
  border-radius: 16px;
  margin-top: 96px;
  width: 1200px;
}

.films-text {
  width: fit-content;
  padding-left: 16px;
  padding-right: 16px;
  height: 50px;
  border-radius: 8px;
  color: var(--white);
  background-color: var(--neon-main);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: -40px;
}

.poster-block {
  margin-top: 32px;
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
}

.image-div {
  position: relative;
}

.poster-image {
  display: block;
  width: 1260px;
  height: 605px;
  object-fit: cover;
  border-radius: 12px;
  background: var(--grey-secondary);
}

.film-overlay {
  background-image: linear-gradient(to top, var(--neutral-main), transparent);
  bottom: 0;
  content: "";
  left: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 90%;
}

.poster-data {
  z-index: var(--first-layer);
  margin-top: calc(-23% + 2.5rem);
}

.text-data {
  margin-left: 32px;
  margin-right: 32px;
  text-align: center;
}

.poster-actions {
  margin-top: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.accent-button {
  width: 128px;
  height: 50px;
  border-radius: 8px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--neon-main);
  cursor: pointer;
}

.poster-buttons {
  color: white;
  border: 1px solid var(--grey-main);
  border-radius: 8px;
  padding: 16px;
  display: block;
  margin-left: 8px;
  width: fit-content;
  background-color: var(--hover-color);
}

.poster-duration {
  margin-left: 8px;
  font-weight: bold;
}

.poster-carousel {
  height: 48rem;
  position: relative;

  & > ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
}

.slide {
  position: absolute;
  inset: 0;
  opacity: 0;
  transition: 200ms opacity ease-in-out;
  transition-delay: 200ms;
}

.slide[data-active] {
  opacity: 1;
  z-index: var(--first-layer);
  transition-delay: 0ms;
}

.poster-carousel-button {
  position: absolute;
  top: 38rem;
  z-index: var(--second-layer);
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  padding: 0;
  background: var(--neutral-dark);
  transition: transform 0.3s ease;

  & > img {
    margin: 12px;
  }

  &:hover {
    background-color: var(--neutral-light);
  }

  &.prev {
    left: 1rem;
  }

  &.next {
    right: 1rem;
  }
}

.next {
  & > img {
    transform: rotate(180deg);
  }
}

.film-rating {
  display: flex;
  background-color: var(--neutral-main);
  padding: 4px 8px;
  border-radius: 16px;
}

.star {
  width: 16px;
}

.star-container {
  width: 80px;
  height: 20px;
  padding: 2px;
}

.rating-count {
  margin-left: 2px;
  color: var(--grey-secondary);
  font-size: var(--caption);
}

.time-icon {
  width: 16px;
  margin-right: 2px;
}

.genres-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.genre-card {
  display: flex;
  align-content: space-around;
  width: 220px;
  height: 300px;
  margin: 8px 0 8px 0;
  border-radius: 10px;
  box-shadow: 0 0 10px var(--shadow-color);
  background-color: var(--neutral-light);
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  overflow: hidden;
  position: relative;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction: column;
  align-items: flex-start;

  &:hover {
    transform: scale(1.05);
  }
}

.genre-images {
  margin-top: 15px;
  width: 184px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
}

.genre-image {
  margin-bottom: 8px;
  border-radius: 8px;
  width: 83px;
  height: 102px;
  object-fit: cover;
  transition: 0.3s ease-in-out;

  &:hover {
    opacity: 0.8;
  }
}

.genre-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 16px;
}

.genre-name {
  color: white;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
}

.genre-images-overlay {
  width: 178px;
  position: absolute;
  bottom: 66px;
  left: 20px;
  height: 60%;
  background-image: linear-gradient(to top, var(--neutral-light), transparent);
  border-radius: 8px;
}

.slider-buttons {
  align-items: center;
  height: 80px;
  display: flex;
  background-color: var(--neutral-dark);
  border-radius: 8px;
  width: 258px;
  justify-content: space-between;
  margin-top: 16px;
}

.upper-block {
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
}

.first-upper-block {
  display: flex;
  justify-content: space-between;
}

.carousel-button {
  height: 48px;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  padding: 0;
  background: var(--neutral-dark);
  transition: transform 0.3s ease;

  & > img {
    border-radius: 8px;
    background-color: var(--neutral-light);
    padding: 12px;
  }

  &:hover {
    background-color: var(--neutral-light);
  }

  &.prev {
    margin-left: 16px;
  }

  &.next {
    margin-right: 16px;
  }
}

.carousel-shapes {
  display: flex;
  width: 18px;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
}
